.horizontal-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.table-bordered {
  border-collapse: collapse;
}

.table-bordered td {
  border: none;
}

.custom-table-color {
  background-color: rgba(211, 211, 211, 0.133) !important;
}

.boader-dark-gray {
  border-bottom: 1px solid #b0b0b0;
}

.custom-color-success {
  background-color: rgba(58, 196, 127, 0.5) !important;
}

.custom-color-secondary {
  background-color: rgba(108, 117, 125, 0.5) !important;
}

.custom-color-danger {
  background-color: rgba(218, 37, 79, 0.5) !important;
}

.custom-color-warning {
  background-color: rgba(247, 184, 36, 0.5) !important;
}
